* {
    padding: 0;
    margin: 0;
    border: 0;
}

*,
*:before,
*:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

:focus,
:active {
    outline: none;
}

a:focus,
a:active {
    outline: none;
}

aside,
nav,
footer,
header,
section {
    display: block;
}

html,
body {
    height: 100%;
    min-width: 320px;
}
body {
    line-height: 1;
    font-size: 14px;
    font-family: "r";
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    color: #192229;
    background-color: #E5E5E5;
}
input,
button,
textarea {
    font-family: "r";
}
input::-ms-clear {
    display: none;
}
button {
    cursor: pointer;
}
button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

a,
a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

ul li {
    list-style: none;    
}
img {
    max-width: 100%;
    height: auto;
}
h1,h2,h3,h4,h5,h6 {
    font-weight: inherit;
    font-size: inherit;
}
@font-face {
    font-family: 'r';
    src: url('../fonts/Rubik-Regular.ttf');
    font-weight: 400;
}

@font-face {
    font-family: 'rb';
    src: url('../fonts/Rubik-SemiBold.ttf');
    font-weight: 500;

}

@second: #6A7B83;

@marked: #3897F0;
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}
.advertise {
    padding-top: 24px;
}

.advertise__title {
    margin-bottom: 16px;
    padding-left: 30px;
    font-size: 20px;
    line-height: 24px;
}

.advertise__note {
    padding-left: 30px;
    font-size: 16px;
    line-height: 135%;
    
    span {
        font-family: "rb";
    }

    a {
        color: @marked;
    }
}

.advertise__table {
    padding-top: 20px;
}



.btn {
    padding-right: 15px;
    padding-left: 15px;
    height: 36px;
    background-color: #fff;
    border-radius: 5px;
    border: 1px solid #D5DFE4;
}

.btn--active {
    background-color: #3897F0;
    color: #fff;
    border: 1px solid #3897F0;
}


.footer {
    width: 1440px;
    margin: 0 auto;
    background-color: #424858;
}

.footer__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 660px;
    margin: 0 auto;
    height: 50px;
}

.footer__text {
    line-height: 17px;
    color: #fff;
}
.head {
    display: flex;
    align-items: center;
    padding-bottom: 25px;
}


.head__title {
    margin-right: 20px;
    font-family: "r";
    font-size: 30px;
    line-height: 28px;
    
}

.head__title--icon {
    display: flex;
    align-items: center;
    
    svg {
        margin-right: 5px;
    }
}

.head__btn:not(:last-child) {
    margin-right: 15px;
}

.header {
    position: relative;

    display: flex;
    align-items: center;
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 165px;
    padding-right: 165px;
    max-width: 1440px;
    margin: 0 auto;
    background-color: #fff;
    
    &:before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 0;
        box-shadow: 0px 3px 10px rgba(0, 73, 129, 0.1);
        z-index: 10;
    }
}


.header__account {
    display: flex;
    align-items: center;
}

.header__imageBlock {
    margin-right: 13px;
}

.header__image {
    width: 44px;
}

.header__accName {
    line-height: 19px;
    font-size: 16px;
}

.header__accTag {
    color: @second;
    line-height: 17px;
}

.header__option {
    display: flex;
    align-items: center;
    margin-left: 60px;
    color: @second;
}

.header__givs {
    color: @second;
}

.header__feedbackBlock {
    display: flex;
    align-items: center;
}

.header__optionItem:not(:last-child) {
    margin-right: 25px;
}

.header__feedback {
    margin-right: 5px;
}


.header__tracker {
    color: @second;
}

.header__logout {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    margin-left: auto;
    background-image: url(../img/svg/image.svg);
    border-radius: 50%;
    padding-left: 1px;
}


.info {
    display: flex;
    padding-top: 40px;
    padding-right: 30px;
    padding-bottom: 30px;
    border-bottom: 2px dashed #D5DFE4;
}


.info__person {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 270px;
    padding-left: 30px;
    padding-right: 30px;
    border-right: 1px solid #D5DFE4;
}

.info__photoBlock {
    width: 90px;
    height: 90px;
    margin-bottom: 12px;
    border-radius: 50%;
    overflow: hidden;
}

.info__name {
    margin-bottom: 8px;
    line-height: 19px;
    font-size: 16px;
    color: @marked;
}

.info__subscr {
    margin-bottom: 12px;
    line-height: 16px;
    color: @second;
}

.info__theme {
    line-height: 18px;
    text-align: center;
}

.info__stat {
    display: flex;
    padding-left: 30px;
    padding-right: 30px;
}

.info__data {
    margin-right: 70px;
}

.info__number {
    margin-bottom: 6px;
    font-size: 30px;
    line-height: 36px;
}

.info__descrip {
    margin-bottom: 21px;
    font-size: 16px;
    line-height: 19px;

    span {
        color: @second;
    }
}

.info__title {
    margin-bottom: 6px;
    font-size: 18px;
    line-height: 21px;
}

.info__screen {
    color: @second;
    line-height: 17px;

    span {
        color: @marked;
    }
}

.info__table {
    margin-top: 26px;
}










//---------table-----------------------------------table------table----------------------------


.table {
    display: flex;
}

.table__column {
    flex-shrink: 0;
}

.table__row {
    line-height: 19px;
    font-size: 16px;
}

.table__column:nth-child(1) {
    width: 180px;
}
.table__column:nth-child(2) {
    width: 235px;
}

.table__row--head {
    color: @second;
    line-height: 16px;
    font-size: 14px;
    margin-bottom: 6px;
}

.table__row--mb {
    margin-bottom: 26px;
}

.table__row--icon {
    display: flex;
    align-items: center;
}

.table__svg {
    margin-right: 3px;
}

.table__tableMarked {
    color: @marked;
}


.main {
    padding-left: 165px;
    padding-right: 165px;
    padding-top: 30px;
    padding-bottom: 80px;
    background-color: #e2edee;
    max-width: 1440px;
    margin: 0 auto;
    
}

.main__body {
    background-color: #fff;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 3px 10px rgba(0, 73, 129, 0.1);
}
.note {
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 12px;
    padding-bottom: 12px;
    background-color: #FFFCE3;
}

.note__text {
    font-size: 14px;
    line-height: 20px;
}

.tableAdv__row {
    display: flex;
    align-items: center;
    height: 70px;
    padding-left: 30px;
    padding-right: 40px;
    border-bottom: 1px solid #D5DFE4;

    &:last-child {
        border-bottom: none;
    }
}

.tableAdv__row--head {
    height: 60px;
    border-bottom: none;
    background-color: #F8F8F8;
}

.tableAdv__column {
    font-size: 14px;
    line-height: 17px;
}

.tableAdv__column--head {
    font-size: 16px;
    line-height: 19px;
}

.tableAdv__column--first {
    width: 210px;
    flex-shrink: 0;
}
.tableAdv__column--second {
    width: 180px;
    flex-shrink: 0;
}

.tableAdve__column--icon {
    display: flex;
    align-items: center;
}

.tableAdv__svg {
    margin-right: 9px;
}

